import React from 'react';
import Link from '../common/Link';

const Breadcrumb = ({arr}) => {
    return (
        arr && arr.length 
        ?
        <ul class="breadcrumb list-inline d-md-none mB20">
            {
            arr.map((d,i)=>(
                <li key={i}>
                    <Link to={d.link} className={arr.length === i+1 && "breadcrumb_active"}>
                        {d.name}
                    </Link>
                </li>
            ))
            }
        </ul>
        :
        null
       
    );
};

export default Breadcrumb;