import React, { Component,useState } from 'react';
import {UrlNameDecode} from '../common/UrlName';
import fetch from '../common/Fetch';
import MainLayout from '../layouts/MainLayout';
import UrlNameEncode from '../common/UrlName';
import BookCard from '../components/BookCard';
import SliderMain from '../components/SliderMain';
import BreadCrumbArrayNw from '../common/BreadCrumbArrayNw';
import Breadcrumb from '../components/Breadcrumb';
import {withContext} from '../contexts/Context';
import Fetch from '../common/Fetch';
import Zoom from 'react-medium-image-zoom'
import 'react-medium-image-zoom/dist/styles.css';
import StarRatings from 'react-star-ratings';
import {Book} from '../common/Icons';



class SingleProduct extends Component {

    state={
        tabs:1,
        relatedBooks:[],
        isLoading:true,
        rating:0,
        ratingMsg:'Rate This Product!'
    }

    componentDidMount(){
        console.log(this.props);
        window.scrollTo(0,0);
        const {proid,catid}=this.props.match.params;
        const {id,name}=UrlNameDecode(proid);

        this.setState({name})
       this.fetchSinglePro(id)
    }

    fetchSinglePro = productID =>{
        window.scrollTo(0,0)
        this.setState({isLoading:true});
        fetch('productDetail',{productID}).then(d=>{
            console.log(d);
            if(d.status || d.status===200){
                this.setState({...d.data,isLoading:false});
            }
        })
    }

    UNSAFE_componentWillReceiveProps(nextProp){
        const {proid}=nextProp.match.params;
        if(proid !== this.props.match.params.proid) {
            const {id,name}=UrlNameDecode(proid);
            this.setState({name})
            this.fetchSinglePro(id);
            return true
        }
        return false
    }

    onBookmark = ( productID,bookmarked ) => {
        Fetch('productBookmark',{productID}).then(d=>{
            if(d.status){
                this.setState({bookmarked:!bookmarked})
            };
        })
    }

    changeTabs = id => this.setState({tabs:id});

    reviewApi=(rating,review,productID)=>{
        let params={rating,review,productID};

        if(!rating){
            delete params['rating']
        }
        if(!review){
            delete params['review'] 
        }
        Fetch('book/saveReview',params).then(d=>{
            console.log(d);
            if(d.status || d.status===200){
                if(rating){
                    this.setState({ratingMsg:'Thank You'}); 
                }else{
                    this.setState({reviewMsg:'Submitted'}); 
                }
                this.setState({bookReview:''});
                setTimeout(() => {
                    this.setState({reviewMsg:''});
                }, 2000);
            }
        })
    }

    changeRating = newRating => {
        const {productID}=this.state;
        this.setState({rating: newRating});
        this.reviewApi(newRating,null,productID);
    }
    onChangeText= e =>{
        const {value,name}=e.target;
        this.setState({[name]:value});
    }

    onSubmitReview = e =>{
        e.preventDefault();
        const {productID,bookReview}=this.state;
        this.reviewApi(null,bookReview,productID);
    }


    render() {

        const {
            tabs,
            imageCDN,
            images,
            imagePath,
            productID,
            bookmarked,
            isbn,
            name,
            description,
            price,
            isLoading,
            relatedBooks,
            author,
            language,
            publisher,
            pages,
            edition,
            effectivePrice,
            cashback,
            sellingPrice,
            binding,
            rating,
            reviewMsg,
            ratingMsg,
            bookReview,
            isPurchased
        }=this.state;

        const {addItemToCart,removeFromCart,cart}=this.props.context;


        return (
           <MainLayout mHeader={{title:name,subTitle:author}} isLoading={isLoading}>
                <section className="single_product text-default section-spacing">
                    <div className="container">
                       <Breadcrumb arr={ BreadCrumbArrayNw(this.props.location,null,'cat')}/>
                        <div className="row">
                            <div className="col-xl-5 col-lg-5 col-md-12">
                                <div className="single_product_img">
                                  

                                        {
                                            images &&  
                                           
                                                  <div className="main_img">
                                                    <Zoom>
                                                    <img src={imageCDN+imagePath+images[0]} alt={name}/>
                                                    </Zoom>
                                                </div>
                                          
                                        }
                                       
                                   
                                    {/* <div className="single_product_img_block">
                                        <ul className="list-block">
                                                    
                                            {
                                               images && images.length>1 ? images.map((d,i)=>(
                                                    i!==0 
                                                    ?
                                                    <li>
                                                        <img src={imageCDN+imagePath+d} alt={name+i}/>
                                                    </li>
                                                    : null
                                                ))
                                                : null
                                            } 
                                        </ul>
                                    </div> */}
                                </div>
                                </div>
                                <div className="col-xl-7 col-lg-7 col-md-12">
                                <div className="single_product_details">
                                    <div className="product_name f-24 f-lg-22 text-uppercase">{name}</div>
                                        <div className="author_name f-14">{author}</div>
                                    {/* <div className="product_rating_review f-13">
                                        <span className="btn btn-radius"><i className="sprite-img star-icon"></i>4.1 </span> 
                                        <span className="">(5 Ratings & 8 Reviews)</span>
                                    </div> */}
                                    <div className="product_price f-26"><span className="text-orange"> ₹ {sellingPrice} </span> <span className="f-15">(MRP {price})</span></div>
                                    <div className="product-offers">
                                        <div><i className="sprite-img offer-icon mR5"></i> Get {cashback} Instant PrepCash</div>
                                        <div class="mT5">Effective Price: <span className="text-green">{effectivePrice}</span></div>
                                    </div>
                                    <div className="btn-group mT20 ">
                                        <button 
                                            className="btn btn-border btn-radius btn-lg mR5 size-sm-40" 
                                            onClick={()=>this.onBookmark(productID,bookmarked)}
                                            > 
                                            {bookmarked ? "UNBOOKMARK" : "BOOKMARK"}
                                        </button>
                                        <button 
                                            className="btn btn-gradient btn-lg btn-radius size-sm-60" 
                                            onClick={ cart[productID] ? ()=>this.props.history.push('/cart') :()=>addItemToCart(productID)}
                                        >
                                            {cart[productID] ? 'GO TO CART' : "ADD TO CART"}
                                        </button>
                                    </div>
                                    <div className="product_brief_detail f-13 mT20">
                                        <h3 className="text-default f-18"><strong>Product Features</strong></h3>
                                        <ul className="list-block">
                                            <li>Language: {language}</li>
                                            <li>ISBN: {isbn}</li>
                                            <li>Binding: {binding}</li>
                                            <li>Publisher: {publisher}</li>
                                            <li>Edition: {edition}</li>
                                            <li>Pages: {pages}</li>
                                        </ul>
                                       {description ? <h3 className="text-default f-18"><strong>About the Book</strong></h3> : null } 
                                        <p dangerouslySetInnerHTML={{__html:description}}/>
                                       
                                    </div>
                                </div>
                            </div>
                        </div>

                                    {
                                        isPurchased ?
                                        <div class="product-features-tab f-14 mT30">
                                        <ul class="list-inline product-features-tab-list text-center">
                                            <li className={tabs===1 && "active"} onClick={() => this.changeTabs(1)}>USER REVIEW</li>
                                        </ul>
                                        <div class="product-features-tab-group">
                                            <div className="user-rw">
                                                <div className="user-rw__l">
                                                    <div className="user-rw__rate">
                                                        <StarRatings
                                                            starDimension="20px"
                                                            starSpacing="2px"
                                                            rating={rating}
                                                            starHoverColor='orange'
                                                            starRatedColor="orange"
                                                            changeRating={this.changeRating}
                                                            numberOfStars={5}
                                                            name='rating'
                                                        />
                                                        <span className='mL10'>{rating}/5</span>
                                                    </div>
                                                    <p>{ratingMsg}</p>
                                                </div>
                                                <div className="user-rw__r">
                                                    <form className="user-rw__form" onSubmit={this.onSubmitReview}>
                                                        <textarea required className='user-rw__form--text' value={bookReview} onChange={this.onChangeText} name="bookReview" rows="10"></textarea>
                                                        {reviewMsg && <p className='pY10'>{reviewMsg}</p>} 
                                                        <input type="submit" className='btn btn-border btn-radius btn-lg mR5  user-rw__form--btn ' value="Post Review"/>
                                                      
                                                    </form>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    : null

                                    }
                       

                        {/* <div class="product-features-tab f-14 mT30">
                            <ul class="list-inline product-features-tab-list text-center">
                                <li className={tabs===0 && "active"} onClick={() => this.changeTabs(0)}>Index</li>
                                <li className={tabs===1 && "active"} onClick={() => this.changeTabs(1)}>Reviews</li>
                                <li className={tabs===2 && "active"} onClick={() => this.changeTabs(2)}>Additional Info</li>
                            </ul>
                            <div class="product-features-tab-group">
                                {
                                    tabs===0
                                    ?
                                    <IndexBlock/>
                                    :
                                    tabs===1
                                    ?
                                    <RatingBlock/>
                                    :
                                    <AdditionalInfo/>
                                }
                            </div>
                        </div> */}
                    </div>


                    {
                    relatedBooks && relatedBooks.length
                    ?
                    <section className="book_card section-spacing">
                        <div className="container  container-md-full">
                            <div className="section-heading">
                                <h2>Recently Added Books</h2>
                            </div>
                            <SliderMain startFromInitial={relatedBooks.length<=2} breakpoints={{ 1024: 4 , 900: 3,600:2 }} defaultSlides={4}>
                                {
                                relatedBooks.map((d,i)=>(
                                    <div key={i}>
                                        <BookCard
                                            to={`${UrlNameEncode(d.productID,d.name)}`} 
                                            style={{margin:'0 1rem'}} 
                                            img={imageCDN+d.imagePath+d.image} 
                                            name={d.name} 
                                            author={d.author} 
                                            price={d.price} 
                                            cashback={d.discount}
                                        />
                                    </div>
                                ))
                                }
                                </SliderMain>
                        </div>
                    </section>
                    : null
                }
                </section>
        </MainLayout>
        );
    }
}

export default withContext(SingleProduct);




