import React, { Component } from "react";
import ReactPaginate from "react-paginate";
import {isMobile} from 'react-device-detect';

const perPageDemo = [10, 20, 50, 100];

class Pagination extends Component {
  state = {
    paginationArr: [],
    currentPage: 0,
    goToPage: 0
  };

  componentDidMount() {
    const { currentPage } = this.props;
    this.setState({ currentPage, goToPage: currentPage + 1 });
  }

  onChnagePerPageList = e => {
    window.scrollTo(0, 0);
    this.props.setPerPage(e.target.value);
    // this.handlePageClick({showPerPage:Number()})
  };

  handlePageClick = data => {
    window.scrollTo(0, 0);
    this.props.getPagination(data);
    this.setState({ goToPage: data.selected + 1 });
  };

  onChangeValue = e => {
    this.setState({ goToPage: e.target.value });
  };

  goToPage = e => {
    const { goToPage } = this.state;

    e.preventDefault();
    this.setState({ currentPage1: 0 });
    if (goToPage >= 1) {
      this.setState({ currentPage1: Number(goToPage - 1) });
      this.handlePageClick({ selected: goToPage - 1 });
    }
  };

  render() {
    const { paginationArr, currentPage1, goToPage } = this.state;
    const { total, perPage, currentPage } = this.props;

    const totalPage = Math.ceil(Number(total) / Number(perPage));
    //   console.log(totalPage);

    return (
        <div class="paginations">
          <ReactPaginate
            previousLabel={ isMobile ? "Back" :`Page ${goToPage} of ${totalPage}`}
            nextLabel={"Next"}
            breakLabel={"..."}
            breakClassName={"break-me"}
            pageCount={totalPage}
            marginPagesDisplayed={2}
            pageRangeDisplayed={5}
            onPageChange={this.handlePageClick}
            initialPage={currentPage}
            forcePage={currentPage1}
            containerClassName={"list-inline user-select-none"}
            activeLinkClassName={"paginations-active"}
          />
        </div>
        
    );
  }
}

export default Pagination;
