class EditProfile extends Component {

  state={
    tab:"editprofile"
  }

  componentDidMount(){
    const {type}=this.props.match.params;
    if(Tabs.filter(d=>d.id===type).length){
      this.setState({tab:type})
    }
  }

  static getDerivedStateFromProps(props, state) {
    if (props.match.params.type !== state.tab) {
      return {
        tab: props.match.params.type
      };
    }
    return null;
  }

  changeTab=id=>this.setState({tab:id})

  render() {
    const {tab}=this.state;
    return (
      <MainLayout>
         <section className="cart section-spacing">
          <div className="container">
            <div className="profile">
              <ul className="list-inline profile-tab">
                {
                  Tabs.map((d,i)=>(
                    <li
                      key={i}
                      className={d.id===tab && "active"}
                    >
                      <Link to={`/user/${d.id}`} style={{display:'block'}} onClick={() => this.changeTab(d.id)}>
                        {d.name}
                      </Link>
                    </li>
                  ))
                }
              </ul>

              <div className="profile-tab-content">
                <div className="row">
                  <div className="col-lg-12 col-xl-12 ">
                    {
                    tab === Tabs[0].id 
                    ? 
                      <EditPro/>
                    : 
                    tab === Tabs[1].id 
                    ? 
                    <Orders/>
                    : 
                    tab === Tabs[2].id 
                    ? 
                    <ProfileAddress />
                     : 
                     tab === Tabs[3].id 
                     ? 
                      <p>Payment</p>
                     : 
                     null
                     }
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </MainLayout>
    );
  }
}

export default EditProfile;




class Orders extends Component {
  state={
    currentTab:0,
    reviewPopup:0,
    orderData:[],
    isLoading:true
  }

  componentDidMount(){
    window.scrollTo(0,0);
    this.setState({isLoading:true})


    Fetch('orders-list').then(d=>{
      // console.log(d);

      if(d.status || d.status===200){
        this.setState({orderData:d.data,isLoading:false})
      }
    })
  }

  setTab = id => this.setState({currentTab:id});
  reviewPopUp= bool =>this.setState({reviewPopup:bool});

  render() {
    const {currentTab,reviewPopup,isLoading,orderData}=this.state;
    // console.log(this.state);
    
    return (
      isLoading
      ?
      <Loader/>
      :
      <div className="cart Delivered-Product">

        {
        orderData && orderData.length
        ?

        orderData.map((d,i)=>(
          <div className="cart-product-box border-full pX20 pY20">
          <div className="row" onClick={() =>this.setTab(currentTab === i ? '' : i)}>
            <div className="col-xl-6 col-lg-6">
            <h2 className="f-18">{d.title}</h2>
              <div className="mT5 text-light f-16">
                {d.subTitle}
              </div>
            </div>
            <div className="col-xl-6 col-lg-6">
              <div className="panel-tab text-right" >
                {/* <input type="radio" id="click-panel" /> */}
                <label
                  for="click-panel"
                  className={`panel_btn ${
                    currentTab === 0 ? "plus-icon" : "minus-icon"
                  }`}
                ></label>
              </div>
            </div>
          </div>
          {currentTab === i ? (
            <div className="panel_content">
              {
                d.items && d.items.length
                ?
                d.items.map((dd,i)=>(
                  <DeliveredProductBox key={i} {...dd} orderStatus={d.title} setReviewPopup={()=>this.reviewPopUp(1)} />
                ))
                : null
              }
             
              <div className="row">
                <div className="col-xl-6 col-lg-6 col-md-12">
                  <OrderDetail {...d.userDetails} />
                </div>
                <div className="col-xl-6 col-lg-6 col-md-12">
                  <PriceDetails fromOrders={1} {...d.priceData}/>
                </div>
              </div>
              {
                reviewPopup ?<Reviews productID={185} onClose={()=>this.reviewPopUp(0)}/> : null
              }
              {/* <Tracking/> */}
    
              
            </div>
          ) : null}
        </div>
        ))
     
        : 
        <div className="">
          NO ORDER FOUND !
        </div>

        }
        
    </div>
    );
  }
}



