const Reviews=({productID,onClose})=>{
  const [review,setText]=useState('');
  const [msg,setMsg]=useState('api pending static id passed');

  const reviewApi=(rating,review,productID,ratedOnService)=>{
    let params={rating,review,productID,ratedOnService};

    if(!rating){
        delete params['rating']
    }
    if(!review){
        delete params['review'] 
    }
    Fetch('book/saveReview',params).then(d=>{
        // console.log(d);
        if(d.status || d.status===200){
          setMsg('Thank You');
            setTimeout(() => {
              setMsg('')
            }, 2000);
        }
    })
  }
  
  const onSubmit= e =>{
    e.preventDefault();
    reviewApi(null,review,productID,ratedOnServiceArr[3].id)
  }
  

  return(
    <Popup onClose={onClose}>
      <div className="feedback">
        <div className="float-right can-click" style={{fontSize:'2rem'}} onClick={onClose}>✕</div>
        <h2 className='text-default text-center feedback__head'>Create Review</h2>
        {
          ratedOnServiceArr.map((d,i)=>(
            <RatingCard key={i} name={d.name} id={d.id} productID={productID} callApi={reviewApi}/>
          ))
        }
        <form className='feedback__form' onSubmit={onSubmit}>
          <textarea  value={review} placeholder={'Write a review'} onChange={(e)=>setText(e.target.vlaue)} required className='feedback__input' rows="7"></textarea>
            <p className='text-red'>{msg}</p>
          <input type="submit" className='feedback__value' value="Submit"/>
 
        </form>
      </div>
    </Popup>
  )
}




const RatingCard=({name,id,callApi,productID})=>{
  const [rating,setRating]=useState(0);

  // rating,review,productID,ratedOnService
  const ratingSet= rate =>{
    setRating(rate)
    callApi(rating,null,productID,id)
  }

  return (
      <div className="feedback__con">
            <h2 className=" feedback__con--head">{name}</h2>
              <StarRatings
                starDimension="20px"
                starSpacing="2px"
                rating={rating}
                starHoverColor='orange'
                starRatedColor="orange"
                changeRating={ratingSet}
                numberOfStars={5}
                name={name}
              />
      </div>
  )
}


