export const UrlNameDecode = data =>{
    if(data && data.length && data.includes('-')){
        const id=data.split('-')[0];
        let name='';
        data.split('-').forEach((d,i)=>{
            if(i!==0){
                name=name.concat(decodeURIComponent(d)+' ')
            }  
        }) 
        return({id,name:name.trim()})
    }
    return data;
}

const UrlNameEncode = (id,name) => {
    if(!name){
        return id
    }
    const value=name.trim().replace(/\s+/g, "-")
    return `${id}-${encodeURIComponent(value.toLowerCase())}`
}

export default UrlNameEncode;
