import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const SliderMain = ({ children, breakpoints, defaultSlides,arrowInside,dots,startFromInitial }) => {
  // const breakpointss={1024:3,600:3,480:2};
  const defaultBrk = defaultSlides ? defaultSlides : 1;

  function gen(obj) {
    if (!obj) return null;
    return Object.entries(obj).map(([brk, no]) => {
      return {
        breakpoint: Number(brk),
        settings: {
          slidesToShow: no,
          arrows: Number(brk) >= 900
        }
      };
    });
  }

  // console.log(gen(breakpoints));

  var settings = {
    dots: dots ,
    infinite: false,
    speed: 500,
    accessibility: false,
    slidesToShow: defaultBrk,
    arrows: true,
    slidesToScroll: 1,
    focusOnSelect:false,
    useTransform:false,
    centerMode:false,
    className:startFromInitial && 'slickNoWrap',
    // centerPadding: "50px",
    nextArrow: <SampleNextArrow arrowInside={arrowInside}/>,
    prevArrow: <SamplePrevArrow arrowInside={arrowInside}/>,
    responsive: gen(breakpoints)
  };

  return <Slider {...settings}>{children}</Slider>;
};

export default SliderMain;

function SampleNextArrow(props) {
  const { className, style, onClick,arrowInside } = props;
  // console.log(className);

  return (
    <div
      className={className + " back-arrow"}
      style={{ ...style,right:arrowInside ? '13px' :'-25px'  ,display: "block", filter: "invert(100%)" }}
      onClick={onClick}
    />
  );
}

function SamplePrevArrow(props) {
  const { className, style, onClick,arrowInside } = props;
  
  return (
    <div
      className={className}
      style={{ ...style,left:arrowInside ? '13px' :'-25px', display: "block", filter: "invert(100%)" }}
      onClick={onClick}
    />
  );
}
