import { UrlNameDecode } from './UrlName';

const BreadCrumbArrayNw =( propsLocation ,param,ignoreKey ) => {
    let arr=[{name:'Home',link:'/'}]
    const {pathname}=propsLocation;
    let path='/';

    if(pathname){
        pathname.split('/').forEach((d,i)=>{
            if(d && param){
                arr.push({name:d,link:`${path+d}`});
                path= i===0 ? '/' : path +d+'/';
                
            }
            else if((d && !d.includes('-'))){
                arr.push({name:d,link:`${path+d}`});
                path= i===0 ? '/' : path +d+'/';
            }else if(d.includes('-')){
                arr.push({name:UrlNameDecode(d).name,link:`${path+d}`});
                path=path+d+'/';
            }
        })
    }
    if(ignoreKey){
        return arr.filter(d=>d.name!==ignoreKey)
    }
  
    
    return arr;


}

export default BreadCrumbArrayNw;