import React, { Component, useState, useEffect } from 'react';
import BookCard from "../components/BookCard";
import Fetch from '../common/Fetch';
import UrlNameEncode,{UrlNameDecode} from '../common/UrlName';
import MainLayout from '../layouts/MainLayout';
import Breadcrumb from '../components/Breadcrumb';
import BreadCrumbArrayNw from '../common/BreadCrumbArrayNw';
import QueryString from 'query-string';
import Loading from '../components/Loader';
import {alphaArr} from '../common/Utils';
import Pagination from '../components/Pagination';
import {isMobile} from 'react-device-detect';
import {isUserExist} from '../common/Auth';



// import { logoutUser } from '../common/Auth';


class ProductListing extends Component {

  state={
    isModalShow:1,
    booksList:null,
    catName:null,
    categoryID:null,
    filterData:[],
    totalBooks:0,
    isLoading:true,
    sideFilter:{},
    productLoading:true,
    totalInventory:0,
    currentPage:0,
    perPage:16,
    searchTerm:""
  }

  componentDidMount(){
    window.scrollTo(0,0);
    // console.log(this.props);
    const {catid}=this.props.match.params;
    const {search}=this.props.location;

    Fetch('/admin/searchFilter',{from:'frontListingFilter'}).then(d=>{
      // console.log(d);
      if(d){
        this.setState({filterData:d.data,isLoading:false})
      }
    })

    if(catid !== undefined && catid ){
      const {id,name}=UrlNameDecode(catid);
      this.setState({categoryID:id,catName:name,isLoading:true});
      if(search){
        const {page}=QueryString.parse(search);
        this.setState({currentPage:Number(page)})
        if(QueryString.parse(search).s){
          return this.fetchSearchApi(null,id,page)
        }
      }
      return this.fetchSearchApi(null,id)
      // return this.addDataToFilters(id)
    }

    if(search){
      const {s,page}=QueryString.parse(search);
      let pageRe=page ? Number(page) :0;
      this.setState({searchTerm:s,currentPage:pageRe,catName:s})
      this.fetchSearchApi(s,null,pageRe)
    }
  }


  fetchSearchApi = (searchTerm, searchFilter,counter=0) => {
    // if(searchTerm){
    this.setState({ productLoading: true });
    let params = { search: searchTerm,screen:'frontend', searchFilter: searchFilter,maxPerPage:this.state.perPage, counter };
    if (!searchTerm) {
      delete params["search"];
    }
    if (!searchFilter) {
      delete params["searchFilter"];
    }

    Fetch("admin/search", params).then(d => {
      if (d.status) {
        this.setState({ booksList: d.data, totalBooks:d.data.length,productLoading: false,totalInventory:d.totalInventory, msg: "" });
        // console.log(d.data);
      } else {
        this.setState({ productLoading: false, msg: d.message, booksList: [] });
      }
    });
  };

  componentWillUnmount(){
    document.getElementById('root').style.paddingBottom=0
  }


  // FetchList = encodeString => {
  //   const {id,name}=UrlNameDecode(encodeString);
  //   this.setState({categoryID:id,catName:name,isLoading:true});
  //   Fetch('showCategoriesProduct',{categoryID:id}).then(d=>{
  //     // console.log(d);
  //     if(d.status || d.status===200){
  //       this.setState({booksList:d.data,totalBooks:d.prodCount,isLoading:false})
  //     }
  //   })
  // }

  UNSAFE_componentWillReceiveProps(nextProps){
    if(nextProps.match.params.catid !== this.props.match.params.catid){
      const {id,name}=UrlNameDecode(nextProps.match.params.catid);
       this.setState({categoryID:id,catName:name});
      this.fetchSearchApi(null,id);
      return true
    }
    if(nextProps.location.search !== this.props.location.search){
      const {s}=QueryString.parse(nextProps.location.search);
      if(QueryString.parse(nextProps.location.search).s !== QueryString.parse(this.props.location.search).s){
        this.setState({searchTerm:s,catName:s})
        this.fetchSearchApi(s);
        return true
      }
    }
    return false;
    
  }

  addDataToFilters=(item,objFilters)=>{
    const {sideFilter,searchTerm}=this.state;
    const copy={...sideFilter,...objFilters};
    if(item !== null && item !== undefined){
      if(item in copy){
        delete copy[item]
      }else{
        copy[item]=item
      }
    }
    this.setState({sideFilter:copy});
    if(this.convertKeyToString(copy).length){
      this.fetchSearchApi(searchTerm,this.convertKeyToString(copy))
    }else{
      this.allFilterClear()
    }
  }

  convertKeyToString = obj =>{
    let str='';
    if( !Object.keys(obj).length) return str;
    
    Object.keys(obj).forEach(v=>{
      str=str+v+','
    })
    str = str.slice(0, -1)
    return str
  }

  allFilterClear=()=>{
    const { categoryID, searchTerm}=this.state;
    this.setState({sideFilter:{}})
    this.fetchSearchApi(searchTerm,categoryID)
  }
  
  getPagination = ({selected}) => {
    const { categoryID, searchTerm,catName }=this.state;
      if(searchTerm){
        this.props.history.push(`/search/?s=${searchTerm}&page=${selected}`)
      }else{
        this.props.history.push(`./${UrlNameEncode(categoryID,catName)}?page=${selected}`)
      }
      this.fetchSearchApi(searchTerm,categoryID,selected)
  }

  toggleModel = bool => this.setState({isModalShow:bool});

  render() {
    const {
      booksList,
      categoryID,
      catName,
      totalBooks,
      filterData,
      isLoading,
      sideFilter,
      productLoading,
      totalInventory,
      currentPage,
      perPage
    }=this.state;
    // console.log(sideFilter);
    
 
    return (
      <MainLayout mHeader={{backPath:'/',title:catName,subTitle:`${totalInventory} ${Number(totalInventory) ? "Books": "Book"}`}} isLoading={isLoading}>
      <section className="product_listing text-default section-spacing p-md-0">
        <div className="container">
          <div className="row">
            <div className="col-xl-3 col-lg-3 d-md-none">
              <div className="product_listing_filter">
                <div className="f-18">
                   {/* <h2>{  catName ? `${catName} -` : ''}</h2>   */}
                   <span className="f-14">  {totalInventory} Items </span>
                </div>
                  <h2 className="f-16 mT15">
                      FILTERS
                      {
                        Object.keys(sideFilter).length
                        ?
                        <>
                        <span style={{fontSize:'1.2rem'}}>{Object.keys(sideFilter).length ? ` (${Object.keys(sideFilter).length})` : null }</span>
                        <span style={{float:'right',color:'red',lineHeight:'1.5rem',fontSize:'1.2rem',cursor:'pointer'}} onClick={this.allFilterClear}>Clear All</span>
                        </>
                        : null
                      } 
                  </h2>
                  {
                    Object.keys(filterData).length 
                    ?
                    Object.entries(filterData).map(([key,value])=>(
                      value.length ?
                      <FilterCell title={key} list={value} addDataToFilters={this.addDataToFilters} sideFilter={sideFilter}/>
                      : null
                    ))
                    : null
                  }
              </div>
            </div>
            <div className="col-xl-9 col-lg-9 col-md-12">

              <Breadcrumb arr={BreadCrumbArrayNw(this.props.location,null,'cat')}/>
              {/* <FilterRow/> */}
             
              <div className="book_card">
                <div className="row">
              {     
              productLoading
              ?
              <Loading/>
              :
                booksList && booksList.length
                    ? booksList.map((d, i) => (
                        <div key={i} className="col-xl-3 col-lg-4 col-md-6 col-sm-6 col-xs-6">
                          <BookCard
                            to={ categoryID!==null ? `./${UrlNameEncode(categoryID,catName)}/${UrlNameEncode(d.productID,d.name)}` : `/${UrlNameEncode(d.productID,d.name)}`}
                            img={d.imageCDN+d.imagePath+d.image}
                            name={d.name}
                            author={d.author}
                            price={d.price}
                            cashback={d.cashback}
                            proId={d.productID}
                          />
                        </div>
                      ))
                    : 
                    <NoResultFound/>
                  }
                   
                </div>
                {
                  totalInventory >= perPage
                  ?
                  <div className="row">
                  <Pagination 
                    total={totalInventory} 
                    perPage={perPage} 
                    getPagination={this.getPagination} 
                    currentPage={currentPage}/>
                </div>
                :null

                }
              
              </div>
            </div>
          </div>
        </div>
      {
        isMobile
        ?
        <MobileFilter 
          clearFilter={this.allFilterClear}
          addDataToFilters={this.addDataToFilters} 
          filterData={filterData}
          />
        : null
      }
      </section>

      </MainLayout>
    );
  }
}

export default ProductListing;


const NoResultFound=()=>{

  const [email,setEmail]=useState('');
  const [title,setTitle]=useState('');
  const [author,setAuthor]=useState('');
  const [showForm,setShowForm]=useState(0);
  const [msg,setMsg]=useState('');

  const onSubmit=e=>{
    e.preventDefault();
    let params={email, title, author};
    if(!email) delete params['email'];


    Fetch('book/suggestions',params).then(d=>{
      if(d.status || d.status===200){
        setMsg('Your Request Submitted.')
        setTimeout(() => {
          setShowForm(0)
        }, 2000);
      }else{
        setMsg('Please Try again later !')
      }
    })
  }


  return(
    <div className="">
        {
          showForm
          ?
          <div className="rb">
            
            <form className="rb-form" onSubmit={onSubmit}>
              <p style={{paddingTop:'5rem'}}>Book Details</p>
              {
                !isUserExist() && 
                <input required type="email" placeholder='Email' onChange={(e)=>setEmail(e.target.value)} />

              }
              <input required type="text" placeholder='Book title' onChange={(e)=>setTitle(e.target.value)} />
              <input required type="text" placeholder='Author' onChange={(e)=>setAuthor(e.target.value)} />
              {msg ? <div>{msg}</div> : null}
              <input className='btn btn-gradient btn-radius' style={{padding:'1rem 3rem'}} type='submit' value="SUBMIT"/>
            </form>
        </div>
        : 
        <div className="req-book">
               <h2 style={{padding:'4rem 0',fontSize:'2.5rem',textAlign:'center'}}>NO RESULT FOUND !</h2>
              <h2>Cant find a book ?</h2>
              <p>Don't worry ! Just fill in the details and we will try our best to get back to you as soon as possible.</p>
              <button className='btn btn-gradient btn-radius' onClick={()=>setShowForm(1)}>SUBMIT BOOK DETAILS</button>
          </div>
        }
    </div>
  
  )
}


