import axios from 'axios';
import {commonParams,isUserLogin} from './Auth';
import {baseURL} from './Urls';
// import {ServiceLow} from '../pages/PageNotFound';

export default function Fetch(extendedURL,extendedParams={},url){
    return axios.post( url ? url : baseURL + extendedURL,{...commonParams(),...extendedParams})
        .then(d=>{
            return d.data;
        }).catch(e =>{
            document.write("Service is unavailable. Please try again later !");
            return Promise.reject('Server Error');
        });
}