import React, { Component,useState } from 'react';
import ProfileAddress from "../components/ProfileAddress";
import PriceDetails from "../components/PriceDetails";
import MainLayout from '../layouts/MainLayout';
import Fetch from '../common/Fetch';
import {withContext} from '../contexts/Context';
import {isMobile} from 'react-device-detect';
import Sticky from 'react-sticky-el';
import Link from '../common/Link';
import UrlNameEncode from '../common/UrlName';


const tabsValues=[
    {
        name:'BAG',
        id:0
    },
    {
        name:'ADDRESS',
        id:1
    },
    {
        name:'PAYMENT',
        id:2
    },
]

const convertKeyToStr=obj=>{
  let st='';
  if(!Object.entries(obj).length) return st;

  Object.entries(obj).forEach(([key,value])=>{
    st=st+key+".::."+value+','
  })
  st = st.slice(0, -1);
  return st;
}


class Cart extends Component {
    state={
        tab:0,
        couponCode:'',
        productData:[],
        offersData:[],
        priceData:{},
        checkoutUrl:'',
        isLoading:true,
        totalCartItems:0,
        couponMsg:'',
        addressList:[]
    }

    componentDidMount(){
      window.scrollTo(0,0)
      console.log(this.props);
      this.fetchCart()
    }

    fetchCart = (updateCart , couponCode,loading=true,useCashback) =>{
    
      const {cart} =this.props.context;

      const cartItems=Object.keys(cart).length;
      if(loading) {this.setState({isLoading:true}); }
      this.setState({totalCartItems:cartItems});

      let params={productIDs: updateCart ? convertKeyToStr(updateCart) : convertKeyToStr(cart),useCashback,couponCode}
      if(!couponCode) delete params['couponCode'];
      if(useCashback=== null) delete params['useCashback'];
         
      Fetch('viewCart',params).then(d=>{
        console.log(d);
        this.setState({isLoading:false})
        if(d.status || d.status===200){
          console.log(d);
          

          this.setState({...d.data,checkoutUrl:d.checkoutUrl,partialLoad:false});
          if(couponCode){
            this.setState({couponMsg:d.data.priceData.couponMessage});
          }
        }
    })
    }

    removeItemFromCart= id =>{
      const {cart,removeFromCart}=this.props.context;
      // console.log(id);
      const copyCart={...cart};
      delete copyCart[id];

     
          
      this.partialLoad(true)
      this.fetchCart(copyCart,this.state.couponCode,false)
      removeFromCart(id);
      if(!Object.keys(copyCart).length){
        return this.setState({productData:[], offersData:[],
          priceData:{},
          checkoutUrl:'',})
      } 
    }

    onApplyCoupon = e =>{
      e.preventDefault();
      this.partialLoad(true)
      this.fetchCart(null ,this.state.couponCode,false);
    }

    partialLoad=bool=>this.setState({partialLoad:bool})

    setTab = id => this.setState({tab:id});

    onChangeCoupon = e => this.setState({couponCode:e.target.value});

    activeAddress = data =>this.setState({addressList:data})
      

    onClickCheckout = checkoutUrl => {
      if(this.state.addressList.length){
        window.location.href=checkoutUrl
      }else{
        this.setState({checkoutErr:'No address found !'})
      }
    }

    useCashBack=useCashback=>{
      console.log(useCashback);
      this.partialLoad(true)
      this.fetchCart(null ,this.state.couponCode,false,useCashback);
    }



    render() {
        const {
          tab,
          productData,
          offersData,
          isLoading,
          priceData,
          totalCartItems,
          couponMsg,
          checkoutUrl,
          partialLoad,
          checkoutErr,
          couponCode
        }=this.state;

        return (
            <MainLayout isLoading={isLoading} partialLoad={partialLoad} >
              {
                productData && !productData.length
                ?
                <div style={{minHeight:'60vh',display:'flex',justifyContent:'center',alignItems:'center',fontSize:'2rem'}}>Empty Cart</div>
                :
                <section className="cart section-spacing">
                <div className="container">
                  <div className="cart-tab text-center">
                    <ul className="list-inline">
                        {
                            tabsValues.map((d,i)=>(
                              <li
                                  key={i}
                                  className={tab === d.id && "active"}
                                  onClick={d.id !==2 ? () => this.setTab(d.id) : null}
                              >
                                  {d.name}
                              </li>
                            ))
                        }
                    </ul>
                  </div>
  
                 { 
  
                 <div className="cart-tab-content">
                    <div className="row">
                      <div className="col-xl-7 col-lg-8 col-md-12">
                        {tab === 0 ? (
                          <div className="cart-leftbar">
                            {
                              offersData && offersData.length
                              ?
                              offersData.map((d,i)=>(
                                  <div className={`box-big ${i !==0 && "mT15"}`}>
                                    {d.offerMsg}
                                  </div>
                              ))
                              : null
                            }
                            
                            {/* <div className="box-small mT15">
                              Yah! Free Shipping on this order
                            </div>
                            <div className="box-small mT15">
                              Shop for Rs.500 more to get Express Shipping
                            </div> */}
  
  
                            <div className="row mT30 mB20 f-18 f-lg-15">
                              <div className="col-xl-6 col-lg-6">
                                <p>My Shopping Bag ({totalCartItems} Books)</p>
                              </div>
                              <div className="col-xl-6 col-lg-6 text-right">
                                <p>Total: ₹ {priceData.subTotal}</p>
                              </div>
                            </div>
                            {
                              productData && productData.length
                              ?
                              productData.map((d,i)=>(
                                <CartProductBox {...d} removeFromCart={this.removeItemFromCart} key={i} />
                              ))
                              : null
                            }
                           
                          </div>
                        ) : tab === 1 ? (
                          <div className="cart-leftbar">
                            <ProfileAddress activeAddress={this.activeAddress} />
                          </div>
                        ) : tab === 2 ? (
                          <p>Payment</p>
                        ) : null}
                      </div>
  
  
                      <div className="col-xl-5 col-lg-4 col-md-12">
                        <div className="cart-rightbar">
                          <form className="cart-item-search" onSubmit={this.onApplyCoupon} >
                            <input type="text" required placeholder="Type Coupon Code" onChange={this.onChangeCoupon} />
                            <button
                              className="btn btn-green btn-sm btn-radius text-center"
                            >
                              Apply Coupon
                            </button>
                          </form>
                          {
                            couponMsg ? <div className="text-center color-red pT15 f-14">{couponMsg}</div> : null
                          }
  
  
                          <PriceDetails {...priceData} refreshApi={this.useCashBack}/>
  
  
                          <Sticky disabled={window.innerWidth>=600}  mode="bottom">
                            <div style={{backgroundColor:'white',padding:'10px 0'}}>
                            <div className="row">
                                <div className="col-xl-5 col-lg-7">
                                  <div>
                                    <span className="f-12">Buy for</span>
                                    <span className="f-13">Rs. {priceData.subTotal}</span>
                                  </div>
                                  <div>
                                    <span className="f-12">Effective Price: </span>
                                    <span className="f-13 text-green">Rs. {priceData.effectivePrice}</span>
                                  </div>
                                </div>
                                <div className="col-xl-7 col-lg-5 text-right">
                                  <button
                                    className="btn btn-gradient btn-lg btn-radius"
                                    onClick={ tab===1  ? ()=>this.onClickCheckout(checkoutUrl) : () => this.setTab(1) || window.scrollTo(0,0)}
                                  >
                                    {
                                      tab===0 ? "PROCEED" : " CHECKOUT"
                                    }
                                  </button>
                                </div>
                              </div>
                              <p style={{color:'red',textAlign:'center',fontSize:'1.4rem',paddingTop:'2rem'}}>{checkoutErr}</p>
                            </div>
                            
                          </Sticky>
                            
                         
                          
                        </div>
                      </div>
                    </div>
                  </div>}
  
  
                </div>
              </section>
              }
           
            </MainLayout>
        );
    }
}

export default withContext(Cart);

const CartProductBox = ({imageCDN,productID,cashback,author,name,sellingPrice,price,discount,image,imagePath,quantity,prepCash,effectivePrice,removeFromCart}) => (
  <div className="cart-product-box border-full pX15 pY15">
    <div className="row">
      <div className="col-xl-3 col-lg-3 col-xs-2">
        <Link to={UrlNameEncode(productID,name)}>
        <img src={imageCDN+imagePath+image} alt={name} />
        </Link>
      </div>
      <div className="col-xl-9 col-lg-9 col-xs-10">
        <div class="book_info" style={{height:'unset'}}>
          <div class="book-category-detail">
            <div className="row">
              <div className="col-xl-8 col-lg-8">
                <div class="book-name f-17">
                  {name}
                </div>
                <div class="book-writer text-light f-14 f-sm-14">
                  {author}
                </div>
              </div>
              <div className="col-xl-4 col-lg-4 text-right">
                <div class="book-price">
                  <span class="f-15 mR5">Rs. {price}</span>
                </div>
              </div>
            </div>
            <div className="row mT20 mB20 md-mY-10">
              <div className="col-xl-6 col-lg-5">
                  <div class="f-14">Quantity: {quantity}</div>
              </div>
              <div className="col-xl-6 col-lg-7 text-right">
                <div class="f-14">
                    {cashback ? `${cashback} PrepCash` : null}<br />
                  <span class="text-light">
                   {isMobile ? "Price :" :"Effective Price:"}  <span className="text-green">Rs. {effectivePrice}</span>
                  </span>
                </div>
              </div>
            </div>
            <div className="border-top pT15">
              <div className="row">
                <div className="col-xl-5 col-lg-4">
                  <button onClick={()=>removeFromCart(productID)} class="f-13 text-red can-click">REMOVE</button>
                </div>
                {/* <div className="col-xl-7 col-lg-8">
                  <a class="text-default f-13">MOVE TO BOOKMARKS</a>
                </div> */}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
);
