import React, { lazy } from "react";
import { Redirect } from "react-router-dom";
import { isUserExist } from "./Auth";
import { LoginRef } from "./LoginRefs";
import Home from "../pages/Home";
import ProductListing from "../pages/ProductListing";
import SingleProduct from "../pages/SingleProduct";
import Cart from "../pages/Cart";
import EditProfile from "../pages/EditProfile";
import Bookmark from '../pages/Bookmarks';
import Login from '../pages/Login';
import Aboutus from '../pages/Aboutus';


const RedirectPage = () => (
  <Redirect to="/">{(window.location.href = LoginRef)}</Redirect>
);

// const RedirectPage = () => (
//   <Redirect to="/">{(window.location.href = LoginRef)}</Redirect>
// );

const authRequired = comp => {

  if (isUserExist()) {
    return comp;
  }
  return RedirectPage;
};

const Routes = [
  {
    path: "/",
    component: Home
  },
  {
    path: "/login",
    component: Login
  },
  {
    path: "/aboutus",
    component: Aboutus
  },
  {
    path: "/cart",
    component: authRequired(Cart)
  },
  {
    path: "/bookmark",
    component: authRequired(Bookmark)
  },
  {
    path: "/user/:type",
    component: authRequired(EditProfile)
  },
  {
    path: "/search",
    component: ProductListing
  },
  {
    path: "/cat/:catid/:proid",
    component: SingleProduct
  },
  {
    path: "/cat/:catid",
    component: ProductListing
  },
 
  {
    path: "/:proid",
    component:SingleProduct
  }
 
];

export default Routes;
