import React,{Suspense,useState,useEffect} from 'react';
import { Switch, Route, BrowserRouter as Router} from 'react-router-dom';
import WithTracket from './common/WithTracker';
import Routes from './common/Routes';
import PageNotFound from './pages/PageNotFound';
import TopContext from './contexts/Context';
import Model from './Modal/Model';
import Fetch from './common/Fetch';
import Popup from './components/Popup';
import Cookies from 'js-cookie';


const App = () => {
    const [isPopup,setPopup]=useState(0);
    const [courseData,setCourseData]=useState([]);

    useEffect(()=>{
        if(!Cookies.get('course')){
            Fetch('dashboardCourses').then(d=>{
                if(d.status===200 || d.status){
                    setCourseData(d.data);
                    setPopup(1)
                }
            })
        }
    },[])

    const onSelectCourse = id => {
        Cookies.set('course',id,{expires: 3})
        setPopup(0)
    }

    return (
        <TopContext>
            <Router>
                <Suspense fallback={<p>Loading</p>}>
                    <Switch>
                        {
                            Routes.map((routeInfo,index)=>{
                                return (<Route exact path={routeInfo.path} key={index}  component={WithTracket((props)=><routeInfo.component {...props}/>)}/>)
                            })
                        }
                        <Route component={PageNotFound}/>
                    </Switch>
                </Suspense>
            </Router>

            
              
            
            
            {
                isPopup 
                ?
                <Model>
                    <Popup>
                        <div className="course">
                            <h2 className="course__heading">Select Stream</h2>
                            <ul className="course__list">
                                {
                                    courseData && courseData.length
                                    ?
                                    courseData.map((d,i)=>(
                                        <li className="course__list--item" key={i}>
                                            <button className='btn btn-gradient btn-radius' onClick={()=>onSelectCourse(d.id)}>{d.name}</button>
                                        </li>
                                    ))
                                    : null
                                }
                            </ul>
                        </div>
                    </Popup>
                </Model>
                : null
            }
            
           
        </TopContext>
    );
};

export default App;
