import axios from 'axios';
import {prepURL} from './Urls';
import Cookies from 'js-cookie';
import { Base64 } from 'js-base64';
import {JsonStringCheck} from './Utils';


const course = Cookies.get('course');

const activeUser = Cookies.get('_temp') ? JsonStringCheck(Base64.decode(Cookies.get('_temp'))) : {};

// console.log(activeUser);

const {email,apiKey,fullname,phone} = activeUser;

const isUserExist = () => Cookies.get('_temp') ? true : false;

const getIP = () => localStorage.getItem('ip') ? localStorage.getItem('ip') :'';

const commonParams=()=>{
    if(email && apiKey){
        return {
            ip:getIP(),
            email,
            apiKey,
            course
        }
    }else{
        return { 
            ip:getIP(),
            course
        }
    }
}

const logoutUser = (refresh=false) =>{
    Cookies.remove('_temp');
    Cookies.remove('cart');
    if (refresh)  window.location='/'
}

const checkUser = async () =>{
    return axios.post(prepURL+'checkUser',{email,apiKey}).then(d=>{        
        if(!d.data.status){
            logoutUser()
        }
    }).catch(e =>Promise.reject(false));
}


export {isUserExist,commonParams,checkUser,logoutUser,course,email,fullname,phone};
