import React, { Component } from 'react';
import Model from '../Modal/Model';
import {Logo } from '../common/Icons';
import {withContext} from '../contexts/Context';
import Cookies from 'js-cookie';
import { Base64 } from 'js-base64';
// import QueryString from 'query-string';
import {prepURL} from '../common/Urls';
import Fetch from '../common/Fetch';




class LoginPopup extends Component {

    state={
        email:'',
        password:'',
        errMsg:'',
    }

    onLogin = e =>{
        e.preventDefault();
        const {email,password}=this.state;

        Fetch(null,{ email,password,login: 1,appName:'prepshop'},prepURL+'login').then(data=>{
            if(data.status){
                const encode = Base64.encode(JSON.stringify(data));
                Cookies.set('_temp',encode,{ expires: 2 });
                this.props.context.toggleLoginPopup(false);
                if(this.props.refLink){
                    window.location.href=this.props.refLink
                }else{
                    window.location.reload()
                }
           
            }else{
                this.setState({errMsg:data.message})
            }
        })

    }
    onChange = e =>{
        const {name,value}=e.target;
        this.setState({[name]:value});
    }

   

    render() {
        const {
            toggleLoginPopup,
            isShowLoginPopup
        }=this.props.context;

        const {email,password,errMsg}=this.state;

        return (
            isShowLoginPopup || this.props.isShow
            ?
            <Model>
            <div class="login" id="login-popup">
                 {! this.props.isShow && <div class="login__bg-back"  onClick={()=>toggleLoginPopup(false)}></div>}
                <div class="login__container">
                    <div class="pop-up">
                        <div class="pop-up__left" >
                            {/* <div class="pop-up__left-welcome-box" id='welcome-screen'>
                                    <h4 class="pop-up__welcome">Welcome</h4>
                                    <p class="pop-up__paragraph">Lorem ipsum dolor sit amet consectetur, adipisicing elit. Voluptates, temporibus. Enim officiis excepturi hic, veniam tenetur, optio facilis officia modi esse dolorum tempora minus nesciunt nemo vel deleniti eius cumque!</p>
                                    <div class="pop-up__btns">
                                        <button class="pop-up__btn" id="login-btn">Login</button>
                                        <button class="pop-up__btn" id="signup-btn">Signup</button>
                                    </div>
                                    <button class="pop-up__fb-btn">Continue with facebook</button>
                            </div> */}
                            <div class=" login-box " id='login-box'>
                                {/* <div class="login-box__bck-btn" id='bck-btn'>&#10094;</div> */}
                                <div class="login-box__logo">
                                    <img class="login-box__logo-icon" src={Logo} alt="logo"/>
                                </div>
                                <form onSubmit={this.onLogin} class="login-box__form">
                                    <div class="login-box__fields">
                                            {/* <div class="login-box__field" id="login-box__nameforsignup">
                                                    <input type="text" name="name" id="login-box__name" autocomplete="username" placeholder="Name" class="login-box__input"/>
                                            </div> */}
                                            <div class="login-box__field">
                                                    <input 
                                                        type="email" 
                                                        name="email" 
                                                        onChange={this.onChange} 
                                                        value={email} 
                                                        required 
                                                        id="login-box__email" 
                                                        autocomplete="email" 
                                                        placeholder="Email" 
                                                        class="login-box__input"
                                                        />
                                            </div>
                                            <div class="login-box__field">
                                                    <input 
                                                        type="password" 
                                                        name="password" 
                                                        onChange={this.onChange} 
                                                        value={password} 
                                                        required 
                                                        id="login-box__password" 
                                                        autocomplete="password" 
                                                        placeholder="Password" 
                                                        class="login-box__input"
                                                        />
                                            </div>
                                            {/* <div class="login-box__field " id='login-box__confirmpassforsignup'>
                                                    <input type="password" name="confirm_password" id="login-box__confirm-password" autocomplete="password" placeholder="Confirm Password" class="login-box__input"/>
                                            </div> */}
                                    </div>
        
                                    <div class="login-box__bottom">
                                            <p class="text-align-center color-orange login-box__msg" id='login-box__msg'>{errMsg}</p>
                                            <div class="login-box__btns" > 
                                                    {/* <input class="login-box__btn" type="button"  value="Signup" /> */}
                                                    <input class="login-box__btn login-box__btn-active"  id="submit-btn" type="submit" value="Login" />
                                            </div>
                                            <a href="https://prepladder.com/login.html?page=forgot"  class="login-box__forget">Forget Password?</a>
                                            
                                    </div>
                                    
                                </form>
                            </div>
                        </div>
                        <div class="pop-up__right">
                            {
                                 ! this.props.isShow &&
                                 <div class="pop-up__close" id='pop-up__close' onClick={()=>toggleLoginPopup(false)}>
                                <div class="pop-up__close-icon"></div>
                            </div>
                            }
                            
                            <img src="https://storage.googleapis.com/prepladder-images/medical/govKODhHrkY2nMi0kQCI1556798183.png" alt="" class="pop-up__right-img"/>
                        </div>
                    </div>
                </div>
        </div>
    </Model>
    : null
        
           
        );
    }
}

export default withContext(LoginPopup);
