export default withContext(withRouter(Header));

const MobileMenu=({headerData,context})=>{
  const toogleBtnRef=useRef(null);


  const [openDropDown,toggleDropDown]=useState('');


  return(
    <React.Fragment>
       <input type="checkbox" required ref={toogleBtnRef} class="menu-mobile__tick" id="tick" />
            <label for="tick" class="menu-mobile__icon">
              <span></span>
            </label>
            <label class="menu-mobile__bg" for="tick"></label>
            <div class="menu-mobile">
              <div class="menu-mobile-nav">
                <div className="user-details pB15">
                  <div className="user-name">{fullname}</div>
                  <div className="user-email mY3">
                    {email}
                  </div>
                  <div className="user-mobileNo">{phone}</div>
                </div>
                <div className="btn-group mB20">
                  {/* <a href="" className="btn btn-border btn-sm">
                    SIGN UP
                  </a> */}
                  {
                    !isUserExist()
                    ?
                    <button onClick={()=>{context.toggleLoginPopup(true);toogleBtnRef.current.checked=false}} className="btn btn-border btn-sm">
                      LOGIN
                    </button>
                    :
                    null
                  }
                 
                </div>

                {
                  Object.entries(headerData) && Object.entries(headerData).length ?
                    Object.entries(headerData).map(([menuKey,menuValue])=>(
                        menuValue && menuValue.length > 0
                        ?
                        <ul className="list-block" key={menuKey}>
                          <li className="dropdown">
                            <a onClick={()=>toggleDropDown( openDropDown===menuKey ? '' : menuKey)} title="MBBS">
                              {menuKey}
                            </a>
                            {
                              openDropDown===menuKey
                              ?
                              <div className="mobile-submenu">
                                  <ul className="list-block">
                                  {menuValue.map(
                                      (dropdownValue, dropdownKey) => (
                                        <li className="dropdown dropdown-icon-none" key={dropdownKey}>
                                                <Link href={`/cat/${UrlNameEncode(dropdownValue.id,dropdownValue.name)}`}>
                                                  {dropdownValue.name}
                                                </Link>
                                        </li>
                                      )
                                    )}
                                  </ul>
                              </div>
                            : null
                            }
                          </li>
                        </ul>
                      : null
                    ))
                    : null
                }



             
                <ul className="list-block border-top pY15">
                  <li>
                    <Link to="/user/orders" title="Orders">
                      Orders
                    </Link>
                  </li>
                  <li>
                    <Link to="/bookmark" title="Bookmarks">
                      Bookmarks
                    </Link>
                  </li>
                  {/* <li>
                    <Link to="/order" title="Your PrepCash">
                      Your PrepCash
                    </Link>
                  </li> */}
                  <li>
                    <Link to="/user/addresses" title="Saved Addresses">
                      Saved Addresses
                    </Link>
                  </li>
                </ul>

                {
                  isUserExist()
                  ?
                  <button
                    onClick={()=>logoutUser(true)}
                    className="btn btn-default btn-full btn-lg text-center"
                  >
                    LOGOUT
                </button>
                : null

                }
               
              </div>
            </div>
    </React.Fragment>
  )
}
