import React, { Component } from 'react';
import Fetch from '../common/Fetch';
import {email} from '../common/Auth';
import Loader from './Loader';
import axios from 'axios';

const AddressType=['Home',"Work","Other"];

class ProfileAddress extends Component {

  state={
    addressList:[],
    isAddNewAddress:false,
    isLoading:true,
    isEditAddress:false,
    addressType:AddressType[0]
  }

  componentDidMount(){
    this.listOfAddress()
  }

  listOfAddress=()=>Fetch('listOfAddresses').then(d=>{
    // console.log(d);
    this.isLoading(true)
    if(d.status===200 || d.status){
      this.setState({addressList:d.data,isLoading:false});
      this.props.activeAddress && this.props.activeAddress(d.data)
  
      if(!d.data.length){
        this.toggleAddress(true)
      }
    }else{
      this.isLoading(false)
      this.toggleAddress(true)
    }
  });


  isLoading = bool => this.setState({isLoading:bool})
  

  onChangeText = e =>{
    const {name,value,checked}=e.target;
    if(name==='isDefault'){
      return this.setState({[name]:checked});
    }
    if(name==='pincode' && String(value).length<=6 ){
      this.setState({[name]:value});
      return this.indiaPostAPI(value)
    }
    if(value.length <= 34 && name!=='pincode'){
      this.setState({[name]:value});
    }

  }


  indiaPostAPI = value =>{
    axios.get(`https://api.postalpincode.in/pincode/${value}`).then(d=>{
      if( d.data[0].Status==="Success" && d.data && d.data.length && d.data[0].PostOffice[0]){
        const {State,District}=d.data[0].PostOffice[0];
        this.setState({state:State,city:District})
      }
    })
  }
  

  deleteAdd = id =>{
    this.isLoading(true)
    Fetch('deleteAddress',{id}).then(d=>{
      if(d.status){
        this.listOfAddress()
      }
    })
  }

  toggleAddress = bool => this.setState({isAddNewAddress:bool});
  toggleEditAddress = bool => this.setState({isEditAddress:bool});

  onSubmit = ( e ) =>{
    e.preventDefault();
    const {addressType,isDefault,state,address2,city,address1,phone,pincode,name,id}=this.state;
    this.isLoading(true)
    const fetchParams={addressType,state,address2,city,address1,phone,pincode,name,isDefault: isDefault ? 1 : 0,country:'India',id};
    if(id===null || id===undefined){
      delete fetchParams['id']
    }

    Fetch('saveAddress',fetchParams).then(d=>{
      console.log(d);
      if(d.status){
        this.listOfAddress();
        this.toggleAddress(false)
        this.setState({addressType:AddressType[0],isDefault:'',state:'',address2:'',city:'',address1:'',phone:"",pincode:"",name:"",id:null});
        this.toggleEditAddress(false)
      }else{
        this.isLoading(false)
      }

    })
  }

  addressSaveToState = data =>{
    this.toggleEditAddress(true);
    this.setState({...data});
  }


  setDefaultAddress=id=>{
    this.isLoading(true)
    Fetch('makeAddressDefault',{id}).then(d=>{
      if(d.status){
        this.listOfAddress();
      }
    })
  }

  render() {
    const {
      addressList,
      isAddNewAddress,
      isLoading,
      isEditAddress,
      id
    }=this.state;
    // console.log(this.state);
    
    // console.log(this.setState['isDefault']);
    

    return (
      isLoading
      ?
      <Loader/>
      :
      <div className="">
      {
        addressList && addressList.length && !isEditAddress
        ?
        addressList.map((d,i)=>(
        <div key={i} className={ `current-address ${Number(d.isDefault) ? 'border-full-blue' :"border-full"} pY15 pX15 mB10 us-none`}>
          <div onClick={()=>this.setDefaultAddress(d.id)}>
          <div className="address_type text-right"  >

              {
                d.isDefault
                ?
                <span className="btn btn-radius btn-yellow" style={{marginRight:'10px',backgroundColor: '#c7aa06',}}>Default</span>
                : null
              }
              <span className="btn btn-radius btn-default">{d.addressType}</span>
              </div>
              <div className="buyer_name f-18"  >{d.name}</div>

              <div className="address_full f-14" >
              {d.address1}
              <br/>
              {d.address2}
              <br/>
              {d.city},{d.state} {d.pincode}
              <br/>
              {d.country}
              </div>
          </div>
          
          <div className="row">
            <div className="col-xl-6 col-lg-5 col-xs-12 f-13">
              Mobile: <span className="f-15">{d.phone}</span>
            </div>
            <div className="col-xl-6 col-lg-7 col-xs-12 text-right xs-text-left xs-mT10">
              <button onClick={()=>this.addressSaveToState({...d})} className="btn btn-default btn-sm btn-radius">
                EDIT
              </button>
              {/* {
                  !Number(d.isDefault)
                  ?
                  <button onClick={()=>this.setDefaultAddress(d.id)} className="btn btn-green btn-sm btn-radius mL10">
                  SET DEFAULT
                  </button>
                : null
              } */}
              <button onClick={()=>this.deleteAdd(d.id)} className="btn btn-border btn-sm btn-radius mL10">
                REMOVE
              </button>
            </div>
          </div>
        </div>
        ))
        : 
        null
      }
     


      {
        !isEditAddress
        ?
        <div className="add-address border-full pY15 pX15 mT20" onClick={()=>this.toggleAddress(!isAddNewAddress)}>
          <div className="buyer_name f-15 text-uppercase">
            <button className="plus_icon"></button>Add New Address
          </div>
        </div>
        : null
      }
     

      {
        isAddNewAddress || isEditAddress ?
          <div className="new-address-form border-full pY15 pX15 mT20">
                  <h2 class="text-uppercase f-18">User details</h2>
                  <form className="form f-14" onSubmit={this.onSubmit}>
                    <div className="form-group">
                      <div className="row">
                        <div className="col-xl-6 col-lg-6 col-sm-12">
                          <input type="text" required name='name' value={this.state['name']} onChange={this.onChangeText} autoComplete placeholder="Name*" />
                        </div>
                        <div className="col-xl-6 col-lg-6 col-sm-12">
                          <input readOnly type="email" required pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$" name='email' value={email} onChange={this.onChangeText} autoComplete placeholder="Email" />
                        </div>
                      </div>
                    </div>
                    <div className="form-group">
                      <div className="row">
                        <div className="col-xl-6 col-lg-6 col-sm-12">
                          <input type="tel" required name='phone'  value={this.state['phone']} onChange={this.onChangeText} autoComplete placeholder="Phone No." />
                        </div>
                        <div className="col-xl-6 col-lg-6 col-sm-12">
                          <input type="text" required  name='pincode' value={this.state['pincode']} onChange={this.onChangeText} autoComplete placeholder="Pincode" />
                        </div>
                      </div>
                    </div>
                    <div className="form-group">
                      <input type="text" required name='address1' value={this.state['address1']} onChange={this.onChangeText} autoComplete placeholder="Address line 1"/>
                    </div>
                    <div className="form-group">
                      <div className="row">
                        <div className="col-xl-6 col-lg-6 col-sm-12">
                          <input type="text" name='address2' value={this.state['address2']} onChange={this.onChangeText} autoComplete placeholder="Address line 2" />
                        </div>
                        <div className="col-xl-6 col-lg-6 col-sm-12">
                          <input type="text" required name='city' value={this.state['city']} onChange={this.onChangeText} autoComplete placeholder="City/District" />
                        </div>
                      </div>
                    </div>
                    <div className="form-group">
                    <div className="row">
                        <div className="col-xl-6 col-lg-6 col-sm-12">
                          <input type="text" name='state' required value={this.state['state']} onChange={this.onChangeText} autoComplete placeholder="State" />
                        </div>
                        <div className="col-xl-6 col-lg-6 col-sm-12">
                          <input type="text" required readOnly value={'India'} autoComplete placeholder="Country" />
                        </div>
                      </div>
                    </div>
                    <div className="form-group">
                      <div className="row mT20">
                        <div className="col-xl-3 col-lg-3 col-sm-12 sm-mB-10">
                          Address Type
                        </div>
                        {
                          AddressType.map((d,i)=>(
                            <div className="col-xl-3 col-lg-3">
                              <input
                                type="radio"
                                id={"home_type"+i}
                                className="mR5"
                                name="addressType"
                                value={d}
                                checked={this.state['addressType']===d}
                                onChange={this.onChangeText}
                                required
                              />
                              <label for={"home_type"+i}>{d}</label>
                            </div>
                          ))
                        }
                      </div>
                    </div>

                    <div className="mT20 text-center border-top pT20">
                      <input type="checkbox" name="isDefault" checked={this.state['isDefault']} onChange={this.onChangeText} id="make_default" className="mR5" />
                      <label for="make_default">Make this as default</label>
                    </div>

                    <div className="mT20 text-center">
                      <input type="button" onClick={ isEditAddress ? ()=>this.toggleEditAddress(!isEditAddress) :()=>this.toggleAddress(!isAddNewAddress)} className='btn btn-default btn-lg btn-sm btn-radius' value="CANCEL"/>
                      <input type="submit" className='btn btn-default btn-lg btn-sm btn-radius mL10' value={isEditAddress ? "UPDATE ADDRESS" :"SAVE NEW ADDRESS"}/>

                      {/* < href="" className="">
                        
                      </a> */}
                      {/* <a href="" className="btn btn-gradient btn-lg btn-radius mL10">
                        SAVE NEW ADDRESS
                      </a> */}
                    </div>
                  </form>
                </div>
            : null
      }

      
    </div>
    );
  }
}

export default ProfileAddress;


